import { Component, OnInit, OnDestroy, EventEmitter, Output, OnChanges, Input } from '@angular/core';
import { InsurancesService } from '../../../insurance-products/services/insurances.dataservice';
import { InsuranceProductDataService } from '../../../insurance-products/services/insurance-products.dataservice';
import { Subscription } from 'rxjs';
import { Product } from '../../../classes/Product';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { PriceAttribute, Prices } from '../../../classes/PriceAttribute';


@Component({
  selector: 'app-insurance-pricing',
  templateUrl: './insurance-attributes.component.html',
  styleUrls: ['./insurance-attributes.component.sass']
})


export class InsuranceAttributesComponent implements OnDestroy, OnInit, OnChanges {

  isRightOrDown: boolean;
  showPriceCalculator: boolean;
  productListContainBespokeAndPrice: boolean;
  productListBespokeOnly: boolean;
  showPlaceOrder: boolean;

  // Properties
  private subscription: Subscription;
  PriceAttributes: PriceAttribute[] = [];

  private productSubscription: Subscription;
  Products: Product[] = [];

  private loiSubscription: Subscription;
  enteredLoi: string;

  private totalPriceSubscription: Subscription;
  TotalPrice: number = 0;
    isInsurance: boolean;
    public taxSubscription: Subscription;
    TotalTax: number = 0;


  @Output() continueInsuranceClicked = new EventEmitter();
  @Output() backToProductsClicked = new EventEmitter();
  @Output() saveForLaterClicked = new EventEmitter();
  @Output() showBespokeInfo = new EventEmitter();
  @Output() sendFoundPrice = new EventEmitter;
  @Output() removeProductFromList = new EventEmitter;
  @Input() isProductSelected: boolean;
 


  constructor(private _InsuranceService: InsurancesService, private _ProductService: InsuranceProductDataService ) {
  }

  ngOnChanges(_changes): void {
    // Calls ngOnInit once after this is called
    if (this.enteredLoi === '' || this.enteredLoi === undefined) {
      this.enteredLoi = this._InsuranceService.loi;
    }
    if (this.TotalPrice === 0) {
      this.TotalPrice = this._InsuranceService.CurrentPrice;
    }
    if (this.TotalTax === 0) {
      this.TotalTax = this._InsuranceService.CurrentTaxTotal;
    }
    if (this.PriceAttributes.length === 0) {
      this.PriceAttributes = this._InsuranceService.PriceAttributes;
    }
    if (this.Products.length === 0) {
      this.Products = this._InsuranceService.Products;
    }
  }

  ngOnInit(): void {
    this.subscription = this._InsuranceService.PriceAttributeState
      .subscribe((state: PriceAttribute[]) => {
        this.PriceAttributes = state;
      });

    this.productSubscription = this._InsuranceService.ProductState
      .subscribe((state: Product[]) => {
        this.Products = state;
      });

    this.loiSubscription = this._InsuranceService.loiState
      .subscribe((state: string) => {
        this.enteredLoi = state;
      });

    this.totalPriceSubscription = this._InsuranceService.CurrentPriceState
      .subscribe((state: any) => {
        this.TotalPrice = state;
      });
    this. _InsuranceService.isInsuranceState.subscribe(x => {
      this.showPlaceOrder = x
    })

    this.taxSubscription = this._InsuranceService.CurrentTaxState
      .subscribe((state: number) => {
        this.TotalTax = state;
      });


    // Check if any products are currently Bespoke
    this.productListContainBespokeAndPrice = this.CheckForBespokeProductsAndPriceProducts(this.Products);
    this.productListBespokeOnly = this.CheckForBespokeOnly(this.Products);
  }


  ngOnDestroy(): void {
    this.subscription.unsubscribe();
    this.productSubscription.unsubscribe();
    this.loiSubscription.unsubscribe();
    this.totalPriceSubscription.unsubscribe();
  }

  toggleIsRightOrDown() {
    if (!this.isRightOrDown) {
      this.isRightOrDown = true;
    } else {
      this.isRightOrDown = false;
    }
  }

  public CheckForBespokeProductsAndPriceProducts(products: Product[]): boolean {
    const bespoke = products.find(x => x.isBespokeProduct);
    const anyPrices = products.find(x => x.Price !== 0 || x.FromPrice !== 0);
    if (bespoke && anyPrices) {
      return true;
    }
    return false;
  }

  public CheckForBespokeOnly(products: Product[]): boolean {
    const bespoke = products.find(x => x.isBespokeProduct);
    const anyPrices = products.find(x => x.Price !== 0 || x.FromPrice !== 0);
    if (bespoke && !anyPrices) {
      return true;
    }
    return false;
  }


  /// Change event
  public GetPriceFromAttribute(attribute: PriceAttribute, value) {
    const product = this.GetProductFromAttribute(attribute);
    const loiBracket = product.loiBracketValue;

    if (loiBracket === undefined) {
      this.ProductIsBespoke(product, attribute, value);
    } else {

      // Getting Individual product price based off attribute
      const price = this.FindPriceFromAttributes(product, loiBracket, attribute, value);
      // Subtract existing ipt
      this.TotalTax -= this._InsuranceService.getProductTax(product.id);

      // Santity Check - tax and price
      if (this.TotalPrice < 0) {
        this.TotalPrice = 0;
      }
      if (this.TotalTax < 0) {
        this.TotalTax = 0;
      }

      // Getting the new IPT based of the attr price
      const newIpt = this.FindIptFromAttrPrice(price, product.prices, product.id);

      product.Price = price;

      // Adding new found price from attributes with minimum product prices
      this.TotalPrice = this.TotalProductPricesAndAttributePrice(price, attribute, product);
      // Adding new found Ipt
      this.TotalTax += newIpt;

      this.sendFoundPrice.emit(this.TotalPrice);
    }


  }
  public FindIptFromAttrPrice(price: number, prices: Prices[], id: number): number {
    let newIpt = 0;
    const foundPrice = prices.find(x => x.price === price);
    if (foundPrice !== undefined) {
      newIpt = foundPrice.ipt;
      this._InsuranceService.editIptPerProduct(newIpt, id);
      return newIpt;
    }
    return newIpt;
  }

  public FindPriceFromAttributes(product: Product, loiBracket: number, attribute: PriceAttribute, value: any): number {
    const loi = loiBracket.toString();
    const productPriceAttributes = this.PriceAttributes.filter(x => x.productId === product.id);
    const formAttributesPerProduct = new Array<PriceAttribute>();

    productPriceAttributes.forEach(function (formAttr: PriceAttribute) {
      if (formAttr.name === attribute.name) {
        formAttr.value = value;
      } else if (formAttr.value === undefined) { // this logic could be flawed
        formAttr.value = formAttr.options[0].value;
      }
      formAttributesPerProduct.push(formAttr);
    });

    if (formAttributesPerProduct.find(x => x.name !== 'LIMITOFINDEMNITY')) {
      const loiAttr = new PriceAttribute();
      loiAttr.name = 'LIMITOFINDEMNITY';
      loiAttr.value = loiBracket.toString();
      loiAttr.productId = product.id;
      formAttributesPerProduct.push(loiAttr);
    }

    let totalPrice = 0;
    product.prices.forEach(function (p, index) {
      const i = index;
      const price = p.price;
      const matchingAttributes = [];
      let foundPrice = false;
      const attributecount = p.attributes.length;

      p.attributes.forEach(function (attr) {
        if (!foundPrice) {
          formAttributesPerProduct.forEach(function (formAttr) {
            if (attr.name === formAttr.name && attr.value === formAttr.value) {
              matchingAttributes.push(formAttr);
              if (matchingAttributes.length === attributecount) {
                foundPrice = true;
                totalPrice = price;
              }
            }
          });
        }
      });
    });

    return totalPrice;
  }

  public GetProductFromAttribute(attr: PriceAttribute): Product {
    return this.Products.find(x => x.id === attr.productId);
  }

  public ProductIsBespoke(_product: Product, attributeChanged: PriceAttribute, value) {
  }

  public TotalProductPricesAndAttributePrice(attributePrice: number, productAttribute: PriceAttribute, product: Product): number {
    let newTotal = attributePrice;
    const productsExludedPassedIn = this.Products.filter(x => x !== product);

    productsExludedPassedIn.forEach(function (p) {
      newTotal += p.FromPrice;
    });

    newTotal = newTotal;
    return newTotal;
  }

  public deleteThisProduct(pIndex: number, product: Product) {
    this.RemoveProductFromList(product);
  }

  public RemoveProductFromList(product: Product) {
    this.removeProductFromList.emit(product);
  }

  public bespokeAlert() {
    this.showBespokeInfo.emit(true)
  }

  public continueWithInsurance(){
    this.continueInsuranceClicked.emit(true);
  }

  public backToproducts(){
    this.backToProductsClicked.emit(false)
  }

  public saveForLaterAlert(){
    this.saveForLaterClicked.emit(true);
  }
  
  public getSampleFiles(products){
    let self = this
    let url = 'https://dev.clsl.co.uk/Content/PDFs/SamplePolicies/'
    products.forEach(function (product) {
      window.open(url + product.samplefilename);
    })
  }

  public getDemandsAndNeeds(products){
    let self = this
    products.forEach(function (product) {
      self._ProductService.GetDemandsAndNeedsFile(product.demandsAndNeeds[0].demandId)
    })
  }
}
