import { AttributeOption } from '../classes/AttributeOptions';
import { Prices, PriceAttribute } from './PriceAttribute';
import { demandsAndNeeds } from './DemandsAndNeeds';
import { RelatedHazards } from './Hazard';


export class Product {
  name: string;
  prices: Prices[];
  priceAttributes: PriceAttribute[];
  attributes: BaseProductAttributes[];
  description: string;
  id: number;
  groupname: string;
  statementOfFacts: string;
  checked: boolean;
  demandsAndNeeds: demandsAndNeeds[];
  commercial: boolean;
  residential: boolean;
  hideProduct: boolean = false;
  hiddenBySize: boolean = false;
  hiddenPropertyType: boolean = false;
  stateFact: boolean;
  formattedStatementOfFacts: string[];
  relatedhazards: RelatedHazards[];
  loiMinValue: number;
  loiBracketValue: number;
  FromPrice: number;
  Price: number;
  isBespokeProduct: boolean;

  constructor(_name: string, _prices: Prices[], _id: number) {
    this.name = _name;
    this.prices = _prices;
    this.id = _id;
    this.hideProduct = false;
    this.hiddenBySize = false;
    this.hiddenPropertyType = false;
    this.stateFact = false;
  }
}

export class ProductPrice {
  price: number;
  attributes: ProductAttribute[];
  fromPrice: number;

  constructor(_price: number, _attributes: ProductAttribute[], _fromPrice: number) {
    this.price = _price;
    this.attributes = _attributes;
    this.fromPrice = _fromPrice
  }
}

export class ProductAttribute {
  name: string;
  value: string;
  description: string;
  attributeDescription: string;
  options: AttributeOption[]
  productBelongsTo: string;
  constructor(_name: string, _value: string, _desc: string, _attributeDescription: string, _options: AttributeOption[]) {
    this.name = _name;
    this.value = _value;
    this.description = _desc;
    this.attributeDescription = _attributeDescription;
    this.options = _options;
  }
}
export class BaseProductAttributes {
  description: string;
  name: string;
  excluded: boolean;
  hidden: boolean;
  maximum: number;
  minimum: number;
  id: number;
  mutable: boolean;
  required: boolean;
  sortorder: number;
  type: any;
  values: BaseProductAttributesValues[];
}

export class BaseProductAttributesValues {
  default: boolean;
  description: string;
  id: number;
  sortorder: number;
  value: string;
  price: number;
}

export class Tax {
  amount: number;
  productId: number;
}
