import { Injectable } from '@angular/core';
import { PriceAttribute } from 'src/app/classes/PriceAttribute';
import { Product, Tax } from 'src/app/classes/Product';
import { Subject } from 'rxjs';
import { CanActivate, CanActivateChild } from '@angular/router';


@Injectable()
export class InsurancesService implements CanActivateChild {
    constructor() { }

    IsInsuranceComponent: boolean;

    private priceAttributeSubject = new Subject<PriceAttribute[]>();
    PriceAttributeState = this.priceAttributeSubject.asObservable();
    PriceAttributes: PriceAttribute[] = [];

    private productSelectedSubject = new Subject<Product[]>();
    ProductState = this.productSelectedSubject.asObservable();
    Products: Product[] = [];

    private loiSubject = new Subject<string>();
    loiState = this.loiSubject.asObservable();
    loi: string;

    private currentPriceSubject = new Subject<string>();
    CurrentPriceState = this.currentPriceSubject.asObservable();
    CurrentPrice: any;

    private currentTaxSubject = new Subject<number>();
    CurrentTaxState = this.currentTaxSubject.asObservable();
    CurrentTaxTotal: any;
    TaxPerProductArray = new Array<Tax>();
    
    private isInsuaranceSubject = new Subject<boolean>();
    isInsuranceState = this.isInsuaranceSubject.asObservable();

    canActivateChild() {
        if (this.Products.length > 0) {
            return true;
        }
        return false;
    }

    addPriceAttributes(attributes: PriceAttribute[]) {
        this.PriceAttributes = attributes;
        this.priceAttributeSubject.next(<PriceAttribute[]>attributes);
    }

    removePriceAttributes(productId: number) {
        this.PriceAttributes = this.PriceAttributes.filter((_item) => _item.productId !== productId);
        this.priceAttributeSubject.next(<PriceAttribute[]>this.PriceAttributes);
    }

    addProduct(product: Product) {
        this.Products.push(product);
        this.productSelectedSubject.next(<Product[]>this.Products);
    }

    removeProduct(product: Product) {
        this.Products = this.Products.filter((_item) => _item.id !== product.id);
        this.productSelectedSubject.next(<Product[]>this.Products);
    }

    sendLimitOfIndemnityEntered(loi) {
        this.loi = loi;
        this.loiSubject.next(loi);
    }

    setPrice(price) {
        this.CurrentPrice = price;
        this.currentPriceSubject.next(price);
    }

    setIptTax(tax) {
        this.CurrentTaxTotal = tax;
        this.currentTaxSubject.next(tax);
    }

    addTaxPerProduct(_tax, productId) {
        const t = new Tax();
        t.amount = _tax;
        t.productId = productId;
        this.TaxPerProductArray.push(t);
    }

    getProductTax(id: number): number {
        const foundIpt = this.TaxPerProductArray.find(x => x.productId === id);

        if (foundIpt) {
            return foundIpt.amount;
        }

        return 0;
    }

    editIptPerProduct(newIpt: number, id: number): any {
        const tax = this.TaxPerProductArray.find(x => x.productId === id);
        tax.amount = newIpt;
    }

    ClearStates(): any {
        this.loi = '';
        this.CurrentPrice = 0;
        this.CurrentTaxTotal = 0;
        this.Products = new Array<Product>();
        this.TaxPerProductArray = new Array<Tax>();
        this.PriceAttributes = new Array<PriceAttribute>();
    }
    sendNewInsurance(param: boolean) {
        this.isInsuaranceSubject.next(param);
    }
}
