import { BaseProductAttributesValues } from '../classes/BaseProductAttributes';


export class Prices {
    attributes: PriceAttribute[];
    price: number;
    ipt: number;
    productName: string;
    constructor()
    {
        this.attributes = [];
        this.price = 0;
        this.productName = '';
    }
}


export class PriceAttribute {
    description: string;
    excluded: boolean;
    isHidden: boolean;
    belongsToProduct: string;
    productId: number;
    maximum: number;
    minimum: number;
    mutable: boolean;
    id: number;
    name: string;
    htmlType: string;
    value: string;
    isRequired: boolean;
    sortorder: number;
    type: string;
    dataType: string;
    options: BaseProductAttributesValues[];
    selectedValue: string;
    priceRelatedAttribute: boolean;
    readonly: boolean;
}