import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Route, Router } from '@angular/router';
import { Location } from '@angular/common';

import { DataService } from '../../services/dataservice.service';


@Component({
  selector: 'app-leftsidebar',
  templateUrl: './leftsidebar.component.html',
  styleUrls: ['./leftsidebar.component.sass'],
})
export class LeftsidebarComponent implements OnInit {
  username: string;
  //credit: any[];
  credit: string;
  constructor(private _dataService: DataService, private _route: ActivatedRoute, private _l: Location, private _router: Router) {
  }

  ngOnInit() {
    this._dataService.checkUser(false).subscribe((u) => {
      this.username = u.firstname + " " + u.lastname;
    });

    this._dataService.getCredit().subscribe(res => {
      this.credit = new String(res[0].available).split(".")[0];
    });
  }
  public OnLogout() {
    
    this._dataService.logout().subscribe(s => {
      console.log(s);
      
      if (s) {
        this._dataService.changeLoggedInUserAtAppLevel("false");
        localStorage.clear();
        this._router.navigate(['login'])
      }
    });
  }

  public goToPage(page: string)
  {
    this._router.navigate([page]);
  }

}
