import { BrowserModule } from '@angular/platform-browser';
import { NgModule, ErrorHandler } from '@angular/core';
import { HttpModule, Http } from '@angular/http';
import { CommonModule } from '@angular/common';

import { ApolloModule, Apollo } from 'apollo-angular';
import { InMemoryCache } from 'apollo-cache-inmemory';
import { HttpLinkModule, HttpLink } from 'apollo-angular-link-http';
import { createHttpLink } from 'apollo-link-http';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { InsuranceProductsModule } from './insurance-products/insurance-products.module';
import { environment } from '../environments/environment';
import { HttpHeaders, HttpClientModule } from '@angular/common/http';
import { GraphQLModule } from './graphql.module';
import { AuthoriseModule } from './authorise/authorise.module';

import { SpringBoardComponent } from './components/spring-board/spring-board.component';
import { SharedModule } from './authorise/shared/shared.module';
import { UrlGuardService } from './services/auth.service';


@NgModule({
  declarations: [
    AppComponent,
    SpringBoardComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    HttpLinkModule,
    InsuranceProductsModule,
    ApolloModule,
    CommonModule,
    SharedModule,
    AuthoriseModule
  ],
  exports: [
  ],
  providers: [UrlGuardService],
  bootstrap: [AppComponent]
})
export class AppModule {
  constructor(
    private apollo: Apollo,
    private _httpLink: HttpLink,
  ) {
    const link = createHttpLink({ uri: environment.graphQl, credentials: 'include' });

    apollo.create({
      link: _httpLink.create(
        {
          uri: environment.graphQl,
          withCredentials: true,
          headers: new HttpHeaders(
            {
              'apollographql-client-name': 'Web',
              'client-version': '1',
              'Access-Control-Allow-Origin': '*'
            }),
        }),
      cache: new InMemoryCache(),
      connectToDevTools: true,
      defaultOptions: {
        watchQuery: {
          errorPolicy: 'none'
        },
        query: {
          errorPolicy: 'none'
        },
        mutate: {
          errorPolicy: 'none'
        }
      }
    });
  }
}
