import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { ActivatedRoute } from '@angular/router';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import { DataService } from '../../services/dataservice.service';
import { InsuranceProductDataService } from 'src/app/insurance-products/services/insurance-products.dataservice';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.sass']
})
export class LoginComponent implements OnInit {

  error: string = "";
  accountIdError: string = "";
  displayResetPassword: boolean = false;

  loginForm: FormGroup = new FormGroup({
    accountid: new FormControl(),
    username: new FormControl(),
    password: new FormControl()
  });

  changePassword: boolean = false;
  resetKey: string;
  _username: string;
  _password: string;
  _accountid: string;
  isLoading: boolean = false;

  constructor(private _mainservice: DataService, private router: Router, private route: ActivatedRoute, private _insuranceProductsService: InsuranceProductDataService) { }

  ngOnInit() {
  }

  onLogin() {
    let self = this;
    this.error = "";
    this.accountIdError = "";
    this._username = this.loginForm.value.username;
    this._password = this.loginForm.value.password;
    this._accountid = this.loginForm.value.accountid;
    if (!this.isLoginValidated(this._username, this._password, this._accountid)) {
        this.isLoading = true;
        this._mainservice.signinUser(parseInt(this.loginForm.value.accountid), this.loginForm.value.username, this.loginForm.value.password).subscribe(loggedin => {
            this.isLoading = false;
            if (loggedin) {
                localStorage.setItem("accountid", self._accountid);
                localStorage.setItem("username", this._username);

                this._mainservice.changeLoggedInUserAtAppLevel("true");
                this._insuranceProductsService.getProducts(true).subscribe();


                self.router.navigate(["../newcase"])
            }
            else {
                this.error = "The username and password that you entered did not match our records. Please double-check and try again."
            }
        });
    }
}

  ResetPassword(_resetPassword: boolean) {
    alert("reset password");
  }

  private isLoginValidated(username: string, password: string, accountId: string): boolean {
    let noError = false;
    if (username === null || password === null) {
        this.error = "You must enter a username/password."
        noError = true;
    }
    if (accountId === null) {
        this.accountIdError = "You must enter a account id.";
        noError = true;
    }
    return noError;
}


    public onCheckUser() {
        this._mainservice.checkUser().subscribe();
    }

    redirect() {
        this.router.navigate(['CaseFiles']);
    }
    register() {
        this.router.navigate(['/Register']);
    }

}
