@Component({
  selector: 'app-insurance-products',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.sass']
})

export class ProductListComponent implements OnInit, OnDestroy {
  // properties
  InsuranceProducts: Product[] = [];
  loiFilteredProducts: Product[] = [];
  resiProducts: Product[] = [];
  searchResults: Product[] = [];
  commercialProducts: Product[] = [];
  FilterProductsCount: number;
  TotalPrice: number;
  PriceAttributes: PriceAttribute[];
  commercialProductDisplay: boolean;
  residentialProductDisplay: boolean;
  enteredLoiVal: string;
  loiValidation: boolean;
  formattedAmount;
  amount;
  showSearchBar: boolean;
  searchProduct = new FormControl();
  searchedValue: string;
  isProductSelected: boolean;
  iptTaxTotal: number;
  showingBespokeAlert: boolean;
  showingSaveForLater: boolean;
  NoProductsMessage: string;
  showPolicyDetail: boolean = false;

  // variables declared by TJ
  productType: string;
  ResProductDescriptionState: boolean[];
  ComProductDescriptionState: boolean[];

  isInsurance: any;

  constructor(private _insuranceService: InsurancesService, private _formBuilder: FormBuilder,
    private _productService: InsuranceProductDataService, private currencyPipe: CurrencyPipe,
    private route: ActivatedRoute, private _router: Router) {
  }

  ngOnInit() {
    this.TotalPrice = 0;
    this.iptTaxTotal = 0;
    this.FilterProductsCount = 0;
    this.PriceAttributes = [];
    this._productService.getProducts(true).subscribe(
      data => this.InsuranceProductsInstantiated(data),
      error => this.error(error)
    );
    this.isInsurance = this._insuranceService.isInsuranceState.subscribe(x => {
      this.showPolicyDetail = x;
    });

  }

  ngOnDestroy() {
    this.TotalPrice = 0;
    this.iptTaxTotal = 0;
    this.FilterProductsCount = 0;
    this.PriceAttributes = [];
    this._insuranceService.ClearStates();
  }

  public error(error) {
    console.log('An Error occured');
    console.log(error);
  }

  public InsuranceProductsInstantiated(products: Product[]) {
    this.InsuranceProducts = products;
  }

  /**
  * Entered Loi this is tied to an input (event function)
  */
  public EnteredLoi(loiValue: string, element) {
    loiValue = loiValue.replace('', '');
    loiValue = loiValue.replace(',', '');
    loiValue = parseInt(loiValue).toString();
    this.NoProductsMessage = '';
    if (loiValue === '' || loiValue === undefined || loiValue === 'NaN') {
      this.ResetPriceProductAttrAll();
    } else {
      this.loiValidation = false;
      this.loiFilteredProducts = this.FilterProductsByLoI(loiValue, this.InsuranceProducts);
      // Now Need to filter by residential and commerical
      this.resiProducts = this.loiFilteredProducts.filter((_item) => _item.residential === true);
      this.commercialProducts = this.loiFilteredProducts.filter((_item) => _item.commercial === true);

      // Sorting alphabetically 
      this.resiProducts.sort(function (a, b) {
        if (a.name < b.name) { return -1; }
        if (a.name > b.name) { return 1; }
        return 0;
      });

      this.commercialProducts.sort(function (a, b) {
        if (a.name < b.name) { return -1; }
        if (a.name > b.name) { return 1; }
        return 0;
      });

      this.loiFilteredProducts.sort(function (a, b) {
        if (a.name < b.name) { return -1; }
        if (a.name > b.name) { return 1; }
        return 0;
      });

      this.enteredLoiVal = loiValue;
      this.PassLimitOfIndemnity(loiValue);
    }

    this.ResProductDescriptionState = new Array(this.resiProducts.length);
    this.ComProductDescriptionState = new Array(this.commercialProducts.length);
  }

  public ResetPriceProductAttrAll() {
    const self = this;
    this._insuranceService.Products.forEach(function (product) {
      product.isBespokeProduct = false;
      product.Price = 0;
      product.checked = false;
      self._insuranceService.removeProduct(product);
      self.TotalPrice = 0;
      self.RemoveAttributes(product.id);
    });
    this.isProductSelected = false;
    this.commercialProductDisplay = false;
    this.residentialProductDisplay = false;
    this.enteredLoiVal = '';
    this.TotalPrice = 0;
    this.iptTaxTotal = 0;
    this._insuranceService.setPrice(0);
  }

  public PassLimitOfIndemnity(loiValue: string): any {
    this._insuranceService.sendLimitOfIndemnityEntered(loiValue);
  }

  public showSearch() {

    if (!this.showSearchBar) {
      this.showSearchBar = true
    } else {
      this.searchProduct.reset()
      this.showSearchBar = false
    }
  }

  public reset() {
    this.searchProduct.reset()
  }

  public Search(_searchTerm) {
    this.searchedValue = _searchTerm;
    this.searchProduct.valueChanges.debounceTime(100).distinctUntilChanged().subscribe
      ((search) => {
        if (search == null) {
          this.searchResults = this.loiFilteredProducts
          this.setCommResArray(this.searchResults)
        }
        else if (search.length >= 3) {
          this.searchResults = this._productService.searchEntries(search, this.loiFilteredProducts);
          if (this.searchResults.length > 0) {

            this.setCommResArray(this.searchResults)
          }
        } else {
          this.searchResults = this.loiFilteredProducts
          this.setCommResArray(this.searchResults)
        }
      });

  }

  public setCommResArray(searchResults) {
    this.resiProducts = searchResults.filter((_item) => _item.residential === true);
    this.commercialProducts = searchResults.filter((_item) => _item.commercial === true);
  }
  private FilterProductsByLoI(loiValue: string, unfilteredProducts: Product[]): Product[] {
    const filteredProducts = new Array<Product>();
    const self = this;

    if (loiValue === '') {
      loiValue = '0';
    }

    const newLoiValue = parseInt(loiValue, null);
    unfilteredProducts.forEach(function (product) {
      const productName = product.name;
      const loiValuesPerProduct = new Array<number>();

      // Getting all loi per product - getting all the loi values and storing into a array - loiValuesPerProduct
      if (product.prices !== null) {
        product.prices.forEach(function (p) {
          const price = p.price;
          const loiAttributes = p.attributes.filter(x => x.name === 'LIMITOFINDEMNITY');
          if (loiAttributes !== null) {
            loiAttributes.forEach(function (loi) {
              const loiNumber = parseInt(loi.value, null);
              const duplicateLoi = loiValuesPerProduct.find(x => x === loiNumber);
              if (duplicateLoi === undefined) {
                loiValuesPerProduct.push(loiNumber);
              }
              loi.value = loi.value.toString();
            });
          }
        });
      }

      // A list of all the limit of indemnity options for each product - sorted
      loiValuesPerProduct.sort(function (a, b) { return a - b; });

      // Check all loi less than value enetred
      const foundBandValue = self.FindLoiBandValue(loiValuesPerProduct, newLoiValue);
      if (foundBandValue) {
        const nextBracketValue = self.RoundUpLOIToNextBracket(loiValuesPerProduct, newLoiValue);
        product.loiMinValue = foundBandValue;
        product.loiBracketValue = nextBracketValue;
        filteredProducts.push(product);
      }
    });

    this.FilterProductsCount = filteredProducts.length;
    return filteredProducts;
  }

  public FindLoiBandValue(array, val) {
    for (let i = 0; i < array.length; i++) {
      if (array[i] <= val) {
        return array[i];
      }
    }
  }

  public RoundUpLOIToNextBracket(array, val) {
    for (let i = 0; i < array.length; i++) {
      if (array[i] >= val) {
        return array[i];
      }
    }
  }

  /**
   * SelectedProduct
   * Event triggered by checkbox when product selected
   */
  public ProductSelected(product: Product, checked) {
    // Working out price based of limit of indemnity/essentially the from price
    const loiValue = product.loiBracketValue;
    const productPrices = product.prices;
    // Adding Product
    if (checked) {
      this.isProductSelected = true;
      product.checked = true;
      this._insuranceService.addProduct(product);

      // Building productPrices
      this.RetrieveAttributesForProduct(product.id, product);

      // Getting the from price and the ipt
      product.FromPrice = this.FindPriceFromLOI(loiValue, productPrices);

      if (product.FromPrice === 0) {
        product.isBespokeProduct = true;
      }

      const ipt = this.FindIptFromPrice(product.FromPrice, productPrices, product.id);

      product.Price = product.FromPrice;
      this.TotalPrice += product.FromPrice;
      this.iptTaxTotal += ipt;
    } else {
      // Removing Product
      product.checked = false;
      this._insuranceService.removeProduct(product);
      this.RemoveAttributes(product.id);
      const productTax = this._insuranceService.getProductTax(product.id);
      this.iptTaxTotal -= productTax;

      // If this condition Matches then that means attributes have changed the price
      if (product.Price !== product.FromPrice) {
        // Subtract price based of attr based price
        this.TotalPrice -= product.Price;
        // Set product price back to the from price
        product.Price = product.FromPrice;
      } else {
        this.TotalPrice -= this.FindPriceFromLOI(loiValue, productPrices);
      }
    }

    if (this._insuranceService.Products.length === 0) {
      this.isProductSelected = false;
    }

    this._insuranceService.setPrice(this.TotalPrice);
    this._insuranceService.setIptTax(this.iptTaxTotal);
  }

  /**
 * Finds the from price from the loi
 * gets the price based of loi
 */
  public FindPriceFromLOI(loi: number, prices: Prices[]): number {
    const self = this;
    if (loi === undefined) {
      loi = 0;
    }
    const loiAsString = loi.toString();
    let foundPrice = 0;

    const foundLoiValues = [];
    prices.forEach(function (p) {
      const loiAttributes = p.attributes.filter(x => x.name === 'LIMITOFINDEMNITY');
      const MatchingLoiAttribute = loiAttributes.find(x => x.value === loiAsString);
      if (MatchingLoiAttribute !== undefined) {

        const _price = new Prices();
        _price.price = p.price;
        _price.attributes.push(MatchingLoiAttribute);
        foundLoiValues.push(_price);
      }

      if (foundPrice === 0) {
        const price = p.price;
        const tax = p.ipt;

        if (MatchingLoiAttribute !== undefined) {
          foundPrice = price;
        }
      }
    });

    // Get the lowest price from found loi prices
    if (foundLoiValues.length > 0) {
      const foundPrices = [];
      foundLoiValues.forEach(function (_p: Prices) {
        foundPrices.push(_p.price);
      });

      foundPrice = foundPrices.reduce((a, b) => Math.min(a, b));
    }

    return foundPrice;
  }

  /**
* Finds the ipt price based of the from price found
* gets the  ipt
*/
  public FindIptFromPrice(price: number, prices: Prices[], productId: number): number {
    const priceAttr = prices.find(x => x.price === price);

    if (priceAttr === undefined) {
      return 0;
    }

    this._insuranceService.addTaxPerProduct(priceAttr.ipt, productId);
    return priceAttr.ipt;
  }

  public RetrieveAttributesForProduct(productId: number, product: Product) {
    this._productService.getProductMainAttributesNEW(productId).subscribe(
      data => this.BuildMainPriceAttributes(data, product),
      error => this.error(error)
    );
  }

  /**
   * BuildMainAttributes
   * Populating the price attributes with the main attribute data, this is because "prices" doesnt contain the correct stuff
   */
  public BuildMainPriceAttributes(attributes: BaseProductAttributes[], product: Product) {
    const self = this;
    // This is excluding LoI
    const priceRelatedAttributes = this.GetAllPriceRelatedAttributeNamesFromPrices(product.prices);
    // Need to remove LOI attributes as they arent needed
    attributes = attributes.filter(x => x.name !== 'LIMITOFINDEMNITY');

    priceRelatedAttributes.forEach(function (attrName) {
      const baseAttribute = attributes.find(x => x.name === attrName);

      // this is where need to build a attribute element
      const newPriceAttribute = self.ConvertBaseProductAttributeToAttributeElement(baseAttribute);
      newPriceAttribute.productId = product.id;
      self.PriceAttributes.push(newPriceAttribute);
    });
    this._insuranceService.addPriceAttributes(this.PriceAttributes);
  }

  public GetAllPriceRelatedAttributeNamesFromPrices(prices: Prices[]) {
    const priceRelatedAttributeNames = new Array<string>();

    prices.forEach(function (p) {
      const nonLoiAttributes = p.attributes.filter(x => x.name !== 'LIMITOFINDEMNITY');
      nonLoiAttributes.forEach(function (attr) {
        const duplicateCheck = priceRelatedAttributeNames.find(x => x === attr.name);
        if (!duplicateCheck) {
          priceRelatedAttributeNames.push(attr.name);
        }
      });
    });
    return priceRelatedAttributeNames;
  }

  // Imported from old code
  private WorkoutElementHtmlType(attrElement: BaseProductAttributes): string {

    let htmlInputType = '';

    switch (attrElement.type) {
      case 'AddressCoordinate':
        attrElement.hidden = true;
        htmlInputType = 'hidden';
        break;
      case 'String':
        htmlInputType = 'text';
        break;
      case 'DateTime':
        htmlInputType = 'date';
        break;
      case 'Dictionary':
        htmlInputType = 'select';
        break;
      case 'MapPolygon':
        attrElement.hidden = true;
        htmlInputType = 'hidden';
        break;
      case 'MapPolygons':
        attrElement.hidden = true;
        htmlInputType = 'hidden';
        break;
      case 'Upload':
        htmlInputType = 'file';
        break;
      default:
        attrElement.hidden = true;
        return '';
    }

    return htmlInputType;
  }

  private GetOptionDefaultValue(options: BaseProductAttributesValues[]): string {
    if (options !== undefined) {
      const defaultValue = options.find(x => x.default);

      if (!defaultValue) { return ''; }

      return defaultValue.value;
    }
    return '';
  }

  public OrderProductAttributeOptions(options: BaseProductAttributesValues[]) {
    options.sort((a, b) => {
      if (a.sortorder < b.sortorder) {
        return -1;
      }
      if (a.sortorder > b.sortorder) {
        return 1;
      }
      return 0;
    });
  }

  // need to create a "AttributeElement" type based of the "BaseProductAttributes"
  public ConvertBaseProductAttributeToAttributeElement(attribute: BaseProductAttributes): PriceAttribute {
    const priceAttr = new PriceAttribute();
    priceAttr.dataType = attribute.type;
    priceAttr.name = attribute.name;
    priceAttr.sortorder = attribute.sortorder;
    priceAttr.isHidden = attribute.hidden;
    priceAttr.htmlType = '';
    priceAttr.isRequired = true;
    priceAttr.mutable = attribute.mutable;
    priceAttr.maximum = attribute.maximum;
    priceAttr.minimum = attribute.minimum;
    priceAttr.excluded = attribute.excluded;
    priceAttr.description = attribute.description;
    priceAttr.htmlType = this.WorkoutElementHtmlType(attribute);
    priceAttr.selectedValue = this.GetOptionDefaultValue(attribute.values);
    priceAttr.options = attribute.values;
    this.OrderProductAttributeOptions(attribute.values);

    return priceAttr;
  }
  ///
  public RemoveAttributes(productId: number) {
    this._insuranceService.removePriceAttributes(productId);
    this.PriceAttributes = this._insuranceService.PriceAttributes;
  }

  public UpdatePrice(price) {
    this.TotalPrice = price;
  }

  public transformAmount(element) {
    let amount = this.amount;

    if (amount !== undefined) {
      amount = amount.replace('', '');
      amount = amount.replace(',', '');
      amount = parseInt(amount).toString();
    }
    this.amount = amount;

    if (amount !== 'NaN') {
      this.amount = this.currencyPipe.transform(this.amount, 'GBP', '', '1.0');
      // Remove or comment this line if you dont want 
      // to show the formatted amount in the textbox.
      element.target.value = this.amount;
    } else {
      element.target.value = '';
      this.amount = '';
    }

    // this.EnteredLoi(amount.toString(), element);
  }

  public DisplayProducts(display: boolean, productType: string) {
    if (this.enteredLoiVal !== undefined && this.enteredLoiVal !== '') {
      this.loiValidation = false;
      this.NoProductsMessage = '';

      let productTypeName = '';
      if (productType === 'commerc') {
        this.commercialProductDisplay = display;
        productTypeName = 'Commercial';
      } else {
        this.residentialProductDisplay = display;
        productTypeName = 'Residential';
      }

      if (this.resiProducts.length === 0) {
        this.residentialProductDisplay = false;
        this.NoProductsMessage = 'No ' + productTypeName + ' Products for this LOI, Please enter a higher value.';
      }
      if (this.commercialProducts.length === 0) {
        this.NoProductsMessage = 'No ' + productTypeName + ' Products for this LOI, Please enter a higher value.';
        this.commercialProductDisplay = false;
      }
    } else {
      this.commercialProductDisplay = false;
      this.residentialProductDisplay = false;
      this.loiValidation = true;
    }
  }

  public ShowProductDescription(pid: number, proType: string) {
    if (proType === 'residential') {
      for (var i = 0; i < this.ResProductDescriptionState.length; i++) {
        if (i != pid) {
          this.ResProductDescriptionState[i] = false;
        }
        else {
          this.ResProductDescriptionState[pid] = true;

        }
      }
      this.productType = "residential";
    }
    if (proType === 'commercial') {
      for (let i = 0; i < this.ComProductDescriptionState.length; i++) {
        if (i !== pid) {
          this.ComProductDescriptionState[i] = false;
        } else {
          this.ComProductDescriptionState[pid] = true;
        }
      }
      this.productType = "commercial";
    }
  }

  public onClose(pid: number, proType: string) {
    if (proType === 'residential') {
      this.ResProductDescriptionState[pid] = false;
    }
    if (proType === 'commercial') {
      this.ComProductDescriptionState[pid] = false;
    }
  }

  /**
 * Removed Product form Right panel (red bin click)
 * Event triggered by checkbox when product selected
 */
  public RemoveProductFromList(product: Product) {
    const productType = product.commercial ? 'commerc' : 'resi';

    this.ProductSelected(product, false);

    const checkbox = <HTMLInputElement>document.getElementById('product_' + product.id + '_' + productType);
    checkbox.checked = false;
  }

  /**
 * Show/close bespoke alert
 * Event triggered by info button and X
 */

  public onShowBespokeAlert(event) {
    this.showingBespokeAlert = event
  }
  public onCloseShowBespokeAlert() {
    this.showingBespokeAlert = false
  }

  public onSaveForLaterAlert(event) {

    this.showingSaveForLater = event
  }
  public onCloseSaveForLaterAlert() {
    this.showingSaveForLater = false
  }
  /**
 * Show/close bespoke alert
 * Event triggered by info button and X
 */
  public onContinueWithInsurance(event) {
    if (this._insuranceService.Products.length > 0) {
      this._router.navigate(['newcase/insurance/policydetail'])
    }
  }
  public backToProductList(event) {
    this._router.navigate(['newcase/insurance'])

  }

}
