import { tap, map } from 'rxjs/operators';
import { CanActivate } from '@angular/router/src/utils/preactivation';
import { ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { DataService } from './dataservice.service';

@Injectable({
  providedIn: 'root'
})
export class UrlGuardService implements CanActivate {
  path;
  route;
  constructor(private apollo: Apollo, private http: HttpClient, private _dataservice: DataService, private router: Router) { }
  canActivate(activeRoute: ActivatedRouteSnapshot, state: RouterStateSnapshot) {

      return this.getUser();
  }

  getUser(): Observable<boolean> {
      
      let username = localStorage.getItem("username");
      if (username === "undefined") {
          username = undefined;
      }

      if (!username) {
          this.router.navigate(['/login']);
      }
      else {
          
          return this._dataservice.checkUserLoggedIn().map((result) => {
              return result;
          })
      }
  }
}
