import { Injectable } from '@angular/core';
import { Apollo } from 'apollo-angular';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/observable';
import gql from 'graphql-tag';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/mergeMap';
import { tap, map } from 'rxjs/operators';
import { filter } from 'async';
import { pipe } from '@angular/core/src/render3/pipe';
import { StatementOfFacts } from '../../classes/StatementOfFacts';
import { Case, Draft } from '../../classes/Case';
import { Product } from '../../classes/Product';
import { BaseProductAttributes } from '../../classes/BaseProductAttributes';
import { environment } from 'src/environments/environment';
import { User } from '../../classes/user';

@Injectable()
export class InsuranceProductDataService {
  constructor(private apollo: Apollo, private http: HttpClient) { }
  
  public getProducts(_isInsurance: boolean): Observable<Product[]> {
    return this.apollo.query({
      query: gql(
        `{
          user {
             products(insuranceonly:${_isInsurance}){
               id,
               name,
               groupname,
               prices {
                 attributes{
                   name,
                   value
                 },
                 price,
                 ipt
               },
               relatedhazards {
                keyword
              },
               demandsAndNeeds {
                  demandId,
                  demandFile,
                  demandName
               }
               samplefilename,
               description,
               commercial,
               residential
             }
           }
          }`)
    }).pipe(tap(x => {

    })).map(res => <Product[]>res.data['user']['products']);
  }
  attributes: BaseProductAttributes[] = [];

  public getProductMainAttributes(productId: number): Observable<any[]> {
    let productattributes = gql`{
      user {
        products(productids:[${productId}]){
        id,
        statementOfFacts
        attributes {
          description,
          name,
          excluded,
          hidden,
          maximum,
          minimum,
          id
          mutable,
          required,
          sortorder,
          type,
          values {
            default,
            description,
            id,
            sortorder,
            value
          }
       }
      }
    }
    }`;
    return this.apollo.query({
      query: productattributes,
      fetchPolicy: 'network-only'
    }).pipe(tap(x => {
      if (x.data != null) {
        this.attributes = x.data["user"]["products"][0]["attributes"];
      }
    })).map(res => res.data = this.attributes);
  }


  public getProductMainAttributesNEW(productId: number): Observable<BaseProductAttributes[]> {
    let productattributesQuery = gql`{
      user {
        products(productids:[${productId}]){
        id,
        statementOfFacts
        attributes {
          description,
          name,
          excluded,
          hidden,
          maximum,
          minimum,
          id
          mutable,
          required,
          sortorder,
          type,
          values {
            default,
            description,
            id,
            sortorder,
            value
          }
       }
      }
    }
    }`;
    return this.apollo.query({
      query: productattributesQuery,
      fetchPolicy: 'network-only'
    }).map(res => res.data = res.data["user"]["products"][0]["attributes"]);
  }


  searchProducts(term: Observable<string>, commercialProducts: Product[], residentialProducts: Product[], otherProducts: Product[], favouriteProducts: Product[]): Product[] {
    let allResults = new Array<Product>();
    allResults = allResults.concat(commercialProducts);
    allResults = allResults.concat(residentialProducts);
    allResults = allResults.concat(otherProducts);
    allResults = allResults.concat(favouriteProducts);

    return this.searchEntries(term, allResults);
  }

  searchEntries(term, allResults: Product[]): Product[] {
    term = term.toLowerCase();

    let filteredResults = allResults.filter(x => x.name.toLowerCase().includes(term) || x.description.toLowerCase().includes(term));
    var unique = filteredResults.filter(function (item, i, ar) {
      return ar.indexOf(item) === i;
    });

    return unique;
  }

  getDemandsAndNeeds(demandId: number) {
    let demands = gql`{
      user {
        demandsandneedbyid(demandId: ${demandId})
      }
    }`;
    return this.apollo.query({
      query: demands,
    }).pipe(tap(x => {
    })).map(res => res.data = res.data["user"]["demandsandneedbyid"]);
  }


  GetDemandsAndNeedsFile(demandId: number) {
    this.getDemandsAndNeeds(demandId).subscribe(res => {
      var blob = this.b64toBlob(res, "application/pdf", 512);
      var blobUrl = URL.createObjectURL(blob);
      window.open(blobUrl);
    });
  }

  downloadFile(data: any) {
    var blob = new Blob([data], { type: 'application/pdf' });
    var url = window.URL.createObjectURL(blob);
    window.open(url);
  }

  b64toBlob(b64Data, contentType, sliceSize) {
    contentType = contentType || '';
    sliceSize = sliceSize || 512;

    var byteCharacters = atob(b64Data);
    var byteArrays = [];

    for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      var slice = byteCharacters.slice(offset, offset + sliceSize);

      var byteNumbers = new Array(slice.length);
      for (var i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }

      var byteArray = new Uint8Array(byteNumbers);

      byteArrays.push(byteArray);
    }

    var blob = new Blob(byteArrays, { type: contentType });
    return blob;
  }


  // prompt price
  // create draft
  public createDraft(_user: User, _draft: Draft, _caseref?: string) {
    // check if a caseref has been passed
    if (_caseref) {
      // need to check if case already exists
      const cr = _user.cases.find((c) => c.casereference === _caseref);
      //
      if (cr) {
        // caseref matches with a case, need to add the draft to it

        cr.drafts.push(_draft);
        window.location.reload();
      } else {
        // caseref does not match with any existing case
        // use caseref to create a new case file
        const c = new Case(_caseref, new Date(), [], [], 0, [], null, null);
        c.drafts.push(_draft);
        const u = JSON.parse(localStorage.getItem('user'));
        u.cases.push(c);

        window.location.reload();
      }
    } else {
      // no caseref has been passed, need to autogenerate the case file
      const ref = _user.username + ' ' + (_user.cases.length + 1);
      const c = new Case(ref, new Date(), [], [], 0, [], null, null);
      c.drafts.push(_draft);
      const us = JSON.parse(localStorage.getItem('user'));
      us.cases.push(c);
      localStorage.setItem('user', JSON.stringify(us));
      window.location.reload();
    }
  }


  public StatementOfFactsCantAgree(sofData: StatementOfFacts) {
    return this.http.post(environment.webProxy + 'api/OrderSystem/sof_cant_agree_send_email', {
      ChosenAttributes: sofData.ChosenAttributes,
      Username: sofData.username,
      CompanyCode: sofData.companycode,
      AccountId: sofData.AccountId,
      ProductName: sofData.ProductName,
      Reasons: sofData.Reasons,
      Token: sofData.Token,
      CustomerEmailAddress: "",
      CustomerFirstName: "",
      CustomerSurname: "",
      CustomerTelephone: ""
    }).map(res => {

      return res;
    });
  }
}