import { Order } from './order';
import { Line } from './line';
import { Product } from './product';
import { Documents } from './Documents';
import { Address } from './Address';
import { CaseAddresses } from './caseAddresses';

export class Case { // implements ICase
  CaseId: number;
  casereference: string;
  datecreated: Date;
  orders?: Order[];
  lines?: Line[];
  drafts?: Draft[];
  casedocuments: Documents[];
  // extra
  status?: CaseStatus;
  caseaddresses: CaseAddresses[];
  address?: Address;
  Token: string;
  Reference: string;
  CaseAdd: boolean = false;

  constructor(_casereference: string, _date: Date,_lines:Line[], _orders: Order[], _status: CaseStatus, _drafts: Draft[],_caseaddresses: CaseAddresses[], _address: Address) {
    this.casereference = _casereference;
    this.datecreated = _date;
    this.orders = _orders;
    this.status = _status;
    this.drafts = _drafts;
    this.caseaddresses = _caseaddresses
    this.address = _address;
    this.lines = _lines
  }
}

export enum CaseStatus {
  'Incomplete',
  'Complete'
}

export class Draft {
  id: number;
  dateTime: string | Date;
  products: Product[];
}
