import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './authorise/login/login.component';
import { RegisterComponent } from './authorise/register/register.component';
import { SpringBoardComponent } from './components/spring-board/spring-board.component';
import { ProductListComponent } from './insurance-products/components/product-list/product-list.component';
import { PolicyDetailComponent } from './insurance-products/components/policy-detail/policy-detail.component';
import { UrlGuardService } from './services/auth.service';
import { InsurancesService } from './insurance-products/services/insurances.dataservice';


const routes: Routes = [
  { path: 'login', component: LoginComponent, pathMatch: 'full' },
  { path: 'register', component: RegisterComponent, pathMatch: 'full' },
  { path: 'newcase', component: SpringBoardComponent, canActivate: [UrlGuardService] },

  {
    path: 'newcase/insurance',          
    component: ProductListComponent,
    children: [                          //<---- child components declared here
        {
            path:'policydetail',
            component: PolicyDetailComponent,
            canActivateChild: [InsurancesService]
        }
    ],
    canActivate: [UrlGuardService]
  },
 { path: '**', component: LoginComponent }
];

@NgModule({
  
  imports: [RouterModule.forChild(routes),
    RouterModule.forRoot(routes)
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
