  private isUserLoggedIn = new Subject<string>();
  loggedIn = this.isUserLoggedIn.asObservable();


  constructor(private apollo: Apollo, private http: HttpClient) { }

  changeLoggedInUserAtAppLevel(res) {
    this.isUserLoggedIn.next(res);
  }

  public logout(): Observable<boolean> {
    const signoutMutation = gql`
    mutation{
      signout
    }`;
    return this.apollo.mutate({ mutation: signoutMutation, fetchPolicy: 'no-cache' }).map(r => {
      console.log(r);

      localStorage.clear();
      return <boolean>r['data']['signout'];
    });
  }

  public signinUser(_accountid: number, _username: string, _password: string): Observable<boolean> {
    return this.signInGraph(_accountid, _username, _password).flatMap(res => {
      if (res) {
        return this.signInProxy(_accountid, _username, _password).map(s => s);
      }
      return new Observable<boolean>();
    });
  }

  private signInGraph(_accountid: number, _username: string, _password: string): Observable<boolean> {
    const signinMutation = gql`
       mutation signin($id: Int, $name: String, $pw: String) {
        signin( user: {
            accountid: $id,
            username: $name,
            password: $pw
          })
     }`;
    return this.apollo.mutate({
      mutation: signinMutation,
      variables: {
        id: _accountid,
        name: _username,
        pw: _password
      },
    }).map(data => {
      console.log(data);
      if (data['data']['signin']) {
        return data['data']['signin'];
      }
      else {
        return new Observable<boolean>();
      }
    })
  }

  private signInProxy(_accountid: number, _username: string, _password: string): Observable<boolean> {
    return this.getProxyToken(_accountid, _username, _password).map(res => {
      if (res.success) {
        localStorage.setItem('proxyToken', res.token);
        return true;
      } else {
        return false;
      }
    });
  }

  private getProxyToken(_accountid: number, _user: string, _password: string): Observable<TokenResponse> {
    return this.http.post(environment.webProxy + 'api/auth/validate', {
      CompanyCode: 'CLS',
      User: _user,
      Account: _accountid,
      Password: _password
    }).map(res => {
      console.log(res);
      localStorage.setItem('user', res['user']);

      return new TokenResponse(<boolean>res['success'], <string>res['token']);
    });
  }

  public checkUserLoggedIn(cahce: boolean = true): Observable<boolean> {
    const currentUser = gql
      `query  {
        user {
          firstname
          lastname
          username
        }
      }`;
    if (cahce) {
      return this.apollo.query({ query: currentUser }).map(response => {
        if (response.data) {
          return true;
        }
      })
    } else {
      return this.apollo.query({ query: currentUser, fetchPolicy: 'network-only' }).map(response => {
        if (response.data) {
          return true;
        }
      })
    }
  }

